.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0;
}

.item:hover {
  scale: 1.05;
}

.img {
    height: 96px;
    width: 89px;
    object-fit: cover;
    border-radius: 8px;
}

.userName {
    font-size: 14px;
    color: #4B5563;
    font-weight: 600;
    margin-top: 8px;
    max-width: 70px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}




